import './ategories.css';
import CategoriesJSON from '../../data/categories.json';
import {Link} from "react-router-dom";
import beauty from '../../img/CategoriesLogo/  .svg';
import sport from '../../img/CategoriesLogo/RoundIcons_FreeSet-08.svg';
import health from '../../img/CategoriesLogo/  .svg';
import auto from '../../img/CategoriesLogo/.svg'
import finance from '../../img/CategoriesLogo/Finance.svg'
import animals from '../../img/CategoriesLogo/.svg';
import photo from '../../img/CategoriesLogo/, , .svg';
import study from '../../img/CategoriesLogo/.svg';
import ads from '../../img/CategoriesLogo/,   .svg';
import design from '../../img/CategoriesLogo/  .svg';
import programming from '../../img/CategoriesLogo/  .svg';
import logistics from '../../img/CategoriesLogo/  .svg';
import house from '../../img/CategoriesLogo/    .svg';
import build from '../../img/CategoriesLogo/  .svg';
import party from '../../img/CategoriesLogo/.svg';


function Categories() {
    const categoryList = CategoriesJSON.categories.map((category) =>
        <Link  className={'categoryItem'} to={`/AllCategories/${category.categoriestitle}`}>
            <img className={'categoryImg'} src={
                (category.categoriestitle === "  ") ?
                beauty : (category.categoriestitle === "") ?
                sport : (category.categoriestitle === "") ?
                health : (category.categoriestitle === "") ?
                auto : (category.categoriestitle === "  ") ?
                finance : (category.categoriestitle === "") ?
                animals : (category.categoriestitle === "") ?
                study : (category.categoriestitle === ", , ") ?
                photo : (category.categoriestitle === "  ") ?
                ads : (category.categoriestitle === "  ") ?
                design : (category.categoriestitle === "") ?
                programming : (category.categoriestitle === "  ") ?
                logistics : (category.categoriestitle === "  ") ?
                house : (category.categoriestitle === "  ") ?
                build : (category.categoriestitle === "  ") ?
                party : category.imgId
            } alt="image not found"/>
            <p className={'categoryTitle'}>{category.categoriestitle}</p>
        </Link>
    );

    return (
        <div>
            <nav className={'categoriesNav'} >
                <h2></h2>
                <Link className={'allCategoriesBtn'} to="/AllCategories"> </Link>
            </nav>
            <div className={'categories'}>
                <div className={'categoriesBlock'}>
                    {categoryList}
                </div>
            </div>
        </div>
    );
}

export default Categories;


